.search-form__button_submit { position: relative; z-index: 1; height: 43px; width: 56px; font-size: 0; background-color: #0f4876; border: 0; transition: .25s; cursor: pointer; }

.search-form__button_submit::before, .search-form__button_submit::after { z-index: 2; content: ''; position: absolute; width: 40px; height: 3px; background-color: #fff; transition: .25s; }

.search-form__button_submit::before { top: 47%; left: 8px; transform: rotate(45deg); }

.search-form__button_submit::after { top: 47%; left: 8px; transform: rotate(-45deg); }

.search-form__button_submit:hover, .search-form__button_submit:focus { background-color: #092c49; }

.search-form__button_open { position: absolute; z-index: 3; height: 43px; width: 56px; font-size: 0; border: 0; top: 0; left: 0; height: 100%; background-image: url("../img/../img/icon-search.png"); background-repeat: no-repeat; background-position: center; background-color: #0f4876; transition: .25s; }

.search-form__button_open:hover { background-color: #092c49; }

.search-form__button_open:focus { background-color: #03111c; }

@media only screen and (max-width: 992px) { .search-form__button_open { left: 50%; transform: translateX(-50%); } }

.search-form__button_hide { z-index: -1; }

