.header {
	padding: 10px 0;
	@include image('../img/header-shadow.png');
	@include cover;
	&-logo {
		&__img {
			display: inline-block;
			vertical-align: middle;
			max-width: 200px;
			max-height: 160px;
			width: auto;
			height: auto;
			@include r(1400) {
				max-width: 150px;
				max-height: 100px; }
			@include r(992) {
				max-width: 200px;
				max-height: 135px; }
			@include r(576) {
				max-width: 100%;
				max-height: 100px; } } } }
