.inner-recipe {
	margin-bottom: 30px;
	padding-top: 50px;
	@include image('../img/burger-club-shadow-only.png');
	background-size: 100% auto;
	background-repeat: no-repeat;
	@include r(1200) {
		padding-top: 45px; }
	@include r(992) {
		padding-top: 40px; }
	@include r(768) {
		padding-top: 35px; }
	@include r(576) {
		padding-top: 0; }
	&__content {
		padding: {
			top: 20px;
			bottom: 20px; }
		background-color: rgba(255, 255, 255, .66);
		@include r(1200) {
			padding: {
				top: 15px;
				bottom: 15px; } }
		@include r(768) {
			padding: {
				top: 10px;
				bottom: 10px; } }
		@include r(576) {
			padding: {
				top: 20px;
				bottom: 20px; } } } }


.recipe {
	margin-bottom: 50px;
	@include r(1200) {
		margin-bottom: 40px; }
	@include r(992) {
		margin-bottom: 30px; }
	@include r(768) {
		margin-bottom: 30px; }
	&-img {
		margin-bottom: 5px;
		width: 100%;
		&__img {
			display: inline-block;
			max-width: 100%;
			width: 100%;
			height: auto;
			max-height: 350px;
			@include r(768) {
				max-height: auto; } } }
	&__title {
		margin: 0;
		width: 100%;
		text-align: center;
		font: 700 35px/35px $almoni;
		color: #f15922;
		@include r(1200) {
			font-size: 32px;
			line-height: 32px; }
		@include r(992) {
			font-size: 28px;
			line-height: 28px; }
		@include r(768) {
			font-size: 24px;
			line-height: 24px; }
		@include r(576) {
			font-size: 20px;
			line-height: 20px; } } }
